import { forwardRef, memo, useCallback, useContext, useEffect, useRef, useState } from "react";
import "./Message.css";
import { AuthContext, UserContext } from "../Auth/Auth";
import { goToMessage, toDoubleDigit } from "./Home";
import { getChatColor, Icon, Profile } from "./common";
import MenuItem from "../UI/MenuItem";
import { socket } from "../../App";
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Skeleton, Slide, Zoom } from "@mui/material";
import { ChatContext } from "./ChatContext";
import { useDispatch, useSelector } from "react-redux";
import { handleCall, handleContextMenu, handleEditMessage, handleGoToMessage, handlePinMessage, handlePinnedMessage, handleReplyToMessage, handleUnpinMessage } from "../Stores/UI";
import MessageContextMenu from "./MessageContextMenu";
import { EmojiConvertor } from "emoji-js";
import MessageText, { getMessageText } from "./MessageText";
import MessageSeen from "./Message/MessageSeen";
import MessageMedia from "./Message/MessageMedia";
import MessageProfileMenu from "./MessageProfileMenu";
import { showUserProfile } from "./Pages/UserProfile";
import MessageCall from "./Message/MessageCall";

function Message({ data, prevMsgFrom, nextMsgFrom, prevMsgDate }) {
    const [openDeleteModal, setOpenDeleteModal] = useState(false)

    const isPinned = useRef(data.pin)

    const emoji = new EmojiConvertor()

    const Auth = useContext(AuthContext);
    const User = useContext(UserContext);

    // const isAdmin = useSelector((state) => state.ui.value.activeChat?.isAdmin) // TODO: Replace it to Permissions
    const isAdmin = true

    const MessageEl = useRef()
    const messageText = useRef()
    const messageMedia = useRef()

    const dispatch = useDispatch()

    const isOutMessage = useRef(data.from._id == User._id ? true : false);
    const msgTime = new Date(data.date);

    console.log('Message Rerendered', isOutMessage.current)

    useEffect(() => {
        // console.log('editedd ', data.message)
        // messageText.current.dangerouslySetInnerHTML = handleMessageText()
        // setText(handleMessageText())
        MessageEl.current.removeEventListener('contextmenu', messageMenu)
        MessageEl.current.addEventListener('contextmenu', messageMenu)
        if (document.body.clientWidth <= 480) {
            MessageEl.current.onclick = messageMenu
        }
    }, [data.message, isPinned.current])

    // useEffect(() => {
    //     if (isPinned.current) {
    //         dispatch(handlePinMessage({ title: 'Pinned Message', subtitle: getMessageText(data, User._id), messageId: data._id }))
    //     }
    // }, [isPinned.current])

    const handleContextMenuClose = useCallback(() => {
        dispatch(handleContextMenu())
    }, [])

    const handleReply = useCallback(() => {
        console.log(data)
        dispatch(handleReplyToMessage(data)); handleContextMenuClose()
    }, [data])

    const handleCopy = useCallback(() => {
        copyTextToClipboard(data.message); handleContextMenuClose()
    }, [data.message])

    const handleSave = useCallback(() => {
        messageMedia.current.onSave(); handleContextMenuClose()
    }, [data.media])

    const handlePin = useCallback(() => {
        socket.emit('PinMessage', { token: Auth.authJWT, message: data, pin: !isPinned.current ?? true })
        socket.on('PinMessage', (response) => {
            if (response.ok) {
                if (!isPinned.current) {
                    isPinned.current = true
                    dispatch(handlePinMessage({ title: 'Pinned Message', subtitle: getMessageText(data, User._id), messageId: data._id }))
                } else {
                    isPinned.current = false
                    dispatch(handleUnpinMessage(data._id))
                }
                socket.off('PinMessage')
            }
        })
        handleContextMenuClose()
    }, [data, isPinned.current])

    const handleEdit = useCallback(() => {
        dispatch(handleEditMessage(data)); handleContextMenuClose()
    }, [data])

    const handleDelete = useCallback(() => {
        setOpenDeleteModal(true); handleContextMenuClose()
    }, [data])

    const handleViewProfile = useCallback(() => {
        showUserProfile(data.from, dispatch)
        handleContextMenuClose()
    }, [data])

    const deleteMessage = () => {
        handleClose()
        socket.emit('DeleteMessage', { token: Auth.authJWT, message: data })
    }

    const handleClose = () => {
        setOpenDeleteModal(false)
    }

    async function copyTextToClipboard(text) {
        if ('clipboard' in navigator) {
            // messageText.current.select();
            return await navigator.clipboard.writeText(text);
        } else {
            // messageText.current.select();
            // return document.execCommand('copy');
        }
    }

    const messageMenu = useCallback((e) => {
        if (e.target.closest('a'))
            return;
        e.preventDefault()

        if (!(e.target.closest('.message-reply') || e.target.closest('.message-from-profile') || e.target.closest('.message-media'))) {
            const items = (
                <>
                    <MessageContextMenu
                        canReply={true}
                        canCopy={true}
                        isPhoto={data.type === 'media'}
                        canPin={isAdmin && !isPinned.current}
                        canUnpin={isAdmin && isPinned.current}
                        canEdit={User._id === data.from._id}
                        canDelete={User._id === data.from._id || isAdmin}
                        onReply={handleReply}
                        onCopy={handleCopy}
                        onSavePhoto={handleSave}
                        onPin={handlePin}
                        onEdit={handleEdit}
                        onDelete={handleDelete}
                    />
                </>
            )
            dispatch(handleContextMenu({ items, e }))
        } else if (e.target.closest('.message-from-profile')) {
            const items = (
                <>
                    <MessageProfileMenu
                        user={data.from}
                        canCall={true}
                        onMessage={() => { }}
                        onCall={() => { dispatch(handleCall(data.from)) }}
                        onView={handleViewProfile}
                    />
                </>
            )
            dispatch(handleContextMenu({ items, e }))
        }
    }, [data, isPinned.current])

    var isSameFromPrevMsg = prevMsgFrom === data.fromId
    var isSameFromNextMsg = nextMsgFrom === data.fromId

    var _msgDate = new Date(data.date)
    var _prevMsgDate = new Date(prevMsgDate)

    return <>
        {(_msgDate.getFullYear() !== _prevMsgDate.getFullYear() ||
            _msgDate.getMonth() !== _prevMsgDate.getMonth() ||
            _msgDate.getDate() !== _prevMsgDate.getDate()) && <div className="sticky-date">
                <span>{getDate(data.date)}</span>
            </div>}
        <div className={`Message ${isOutMessage.current ? "Out" : "In"}`} id={data._id} ref={MessageEl} onDoubleClick={handleReply}>
            {!isOutMessage.current && (
                <div className={"message-from-profile" + (isSameFromNextMsg ? ' hidden' : '')}>
                    <Profile name={data.from.firstname} id={data.from._id} size={36} />
                </div>
            )}
            <div className="bubble">
                <div className="body">
                    {!isOutMessage.current && (!isSameFromPrevMsg && data.type !== 'media') && <div className={"from" + getChatColor(data.from._id)}>{data.from.firstname}</div>}
                    {data.reply !== 0 && <div className={"message-reply" + getChatColor(data.reply.from._id)} onClick={() => dispatch(handleGoToMessage(data.reply._id))}>
                        <div className="line"></div>
                        <div className="body">
                            <div className="title">{data.reply.from.firstname}</div>
                            <div className="subtitle"><MessageText data={data.reply} /></div>
                        </div>
                    </div>}
                    <div className="message-text" dir="auto">
                        {data.type === 'media' &&
                            <MessageMedia media={data.media} data={data} ref={messageMedia} />
                        }
                        {data.type === 'call' &&
                            <MessageCall data={data} />
                        }
                        <MessageText data={data} isInChat="true" />
                        <div className="message-details">
                            {data.edited && <span>edited</span>}
                            <div className="message-time">{`${toDoubleDigit(
                                msgTime.getHours()
                            )}:${toDoubleDigit(msgTime.getMinutes())}`}</div>
                            {isOutMessage.current && (
                                <MessageSeen data={data} />
                            )}
                        </div>
                    </div>
                </div>
            </div>
            <Dialog
                open={openDeleteModal}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                PaperProps={{
                    sx: {
                        background: '#0008',
                        backdropFilter: 'blur(25px)',
                        borderRadius: '16px'
                    }
                }}
                sx={{
                    "& > .MuiBackdrop-root": {
                        background: "rgba(0, 0, 0, 0.2)"
                    }
                }}
            >
                <DialogTitle id="alert-dialog-title">
                    {"Do you want to delete this message?"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        This will delete it for everyone in this chat.
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>CANCEL</Button>
                    <Button color="error" onClick={deleteMessage}>
                        DELETE
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    </>;
}

export const getDate = (date) => {
    const _date = new Date(date)
    const now = new Date()
    const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];

    if (_date.getFullYear() === now.getFullYear() || (_date.getMonth() > now.getMonth() && _date.getFullYear() + 1 === now.getFullYear())) {
        if (_date.getMonth() === now.getMonth()) {
            if (_date.getDate() === now.getDate() || (_date.getDate() + 1 === now.getDate() && _date.getHours() + 6 >= now.getHours())) {
                return 'Today'
            } else if (_date.getDate() >= now.getDate() - 7) {
                const weekday = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
                return weekday[_date.getDay()]
            }
        } else {
            return `${months[_date.getMonth()]} ${_date.getDate().toLocaleString('en-US', { minimumIntegerDigits: 2 })}`
        }
    } else {
        return `${months[_date.getMonth()]} ${_date.getDate().toLocaleString('en-US', { minimumIntegerDigits: 2 })}, ${_date.getFullYear()}`
    }
}


export default memo(Message)